import React from 'react'
import { Route, Switch } from 'react-router-dom'

import About from './about.js'
import Skills from './skills.js'
import Projects from './projects.js'
import ProjectOne from './projectOne.js'
import ProjectTwo from './projectTwo.js'
import ProjectThree from './projectThree.js'
import Contact from './contact.js'
import useRouter from '../useRouter'



const Content = () => {
    const { location } = useRouter()

    return (
            <div id="miniContent">
              <Switch>
                <Route exact path="/" component={About} />
                <Route path="/skills" component={Skills} />
                <Route path="/projects" component={Projects} />
                <Route path="/projectone" component={ProjectOne} />
                <Route path="/projecttwo" component={ProjectTwo} />
                <Route path="/projectthree" component={ProjectThree} />
                <Route path="/contact" component={Contact} />
              </Switch>
              {/* <div id="backDiv" /> */}
            </div>
    )
            
    }
    

export default Content