import React, { useState } from 'react'
import ArrowKeysReact from 'arrow-keys-react'


class ProjectOne extends React.Component {


    constructor(){
        super()
        this.state = {
            detailImage: 0,
            hover: false,
            x: 0, 
            y: 0
        }
        this.forward = this.forward.bind(this)
        this.back = this.back.bind(this)
        this.returnDetail = this.returnDetail.bind(this)
        this.returnDetailHover = this.returnDetailHover.bind(this)
        this.toggleHoverOn = this.toggleHoverOn.bind(this)
        this.toggleHoverOff = this.toggleHoverOff.bind(this)
        this.backgroundChange = this.backgroundChange.bind(this)

    }



    forward = () => {
        let newNum = 0
        if (this.state.detailImage === 2){
            newNum = 0
        } else {
            newNum = this.state.detailImage
            newNum++
        }
        this.setState({
            detailImage: newNum
        })
        console.log(this.state.detailImage)
    }

    back = () => {
        let newNum = 0
        if (this.state.detailImage === 0){
            newNum = 2
        } else {
            newNum = this.state.detailImage
            newNum--
        }
        this.setState({
            detailImage: newNum
        })
        console.log(this.state.detailImage)
    }

    returnDetail = () => {
        if (this.state.detailImage === 0) {
            return (
                <iframe width="560" height="315" src="https://www.youtube.com/embed/2Q_PyxAE3Mo" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
            )
        } else if (this.state.detailImage === 1) {
            return (
                <img src="./images/wireheadtrainingstatic.png" alt="detail static" />
            )
        } else if (this.state.detailImage === 2) {
            return (
                <img src="./images/wireheaddatavizstatic.png" alt="detail static" />            
            )
        }
    }

    returnDetailHover = () => {
        if (this.state.detailImage === 0) {
            return (
                <iframe width="560" height="315" src="https://www.youtube.com/embed/2Q_PyxAE3Mo" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
            )
        } else if (this.state.detailImage === 1) {
            return (
                <img src="./images/wireheadtraining.gif" alt="detail" />
            )
        } else if (this.state.detailImage === 2) {
            return (
                <img src="./images/wireheaddataviz.gif" alt="detail" />            
            )
        }
    }

    toggleHoverOn = () => {
        this.setState( {
            hover: true
        })
    }

    toggleHoverOff = () => {
        this.setState({
            hover: false
        })
    }    

    backgroundChange () {
        if (this.state.detailImage === 0) {
            return (
                {
                    backgroundColor: 'rgb(128, 160, 165)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else if (this.state.detailImage === 1) {
            return (
                {
                    backgroundColor: 'rgb(148, 170, 149)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else {
            return (
                {
                    backgroundColor: 'rgb(172, 181, 163)',
                    transition: 'background-color 2s ease-out'

                }
            )
        }
    }

    render(){

        ArrowKeysReact.config({
            left: () => {
                if (this.state.detailImage === 0 ) {
                    this.setState({
                        detailImage: 2
                    })
                } else {
                    let tempnum = this.state.detailImage
                    tempnum--
                    this.setState({
                        detailImage: tempnum
                    })
                }
              },
            right: () => {
              if (this.state.detailImage === 2 ) {
                  this.setState({
                      detailImage: 0
                  })
              } else {
                  let tempnum = this.state.detailImage
                  tempnum++
                  this.setState({
                      detailImage: tempnum
                  })
              }
            }
        })

    // const backgroundColor = {
    //     width: `${(props.x / 50) + 20}%`
    // }
    let xValue = Math.round((this.state.x / 6.27) / 8)
    let yValue = Math.round((this.state.y / 4) / 8)



    console.log(xValue, yValue)
    
    return(
        <div id="projectOne" style={this.backgroundChange()}>
            <div className="projectItemTop">
                    <div className="detailNav" onClick={this.back}>
                        <div>
                            <img src="./images/rewind.svg" alt="back" className="noSelect"  />
                        </div>
                    </div>
                    <div className="detailImage" onMouseEnter={this.toggleHoverOn} onMouseLeave={this.toggleHoverOff}>
                        <div id="detailImageContainer">
                            {this.state.hover === false ? this.returnDetail() : this.returnDetailHover() }
                        </div>
                    </div>
                    <div className="detailNav"  onClick={this.forward}>
                        <img src="./images/fast-forward.svg" alt="forward" className="noSelect"/>
                    </div>
            </div>
            <div className="projectItemBottom">
                <div className="projectItemBottomBox">
                    <div className="bottomIcon" >
                        {this.state.detailImage === 0 ? <img src="./images/chromeHighlight.svg" alt="google chrome icon" /> : <img src="./images/chrome.svg" alt="google chrome icon" />}
                    </div>
                    <div className="bottomLeftText">

                        <h3>
                            Wirehead is a Google Chrome Extension that helps you understand your browsing behavior.  
                        </h3>
                    </div>
                </div>
                <div className="projectItemBottomBox">
                    <div className="bottomIcon">
                        {this.state.detailImage === 1 ? <img src="./images/cpuHighlight.svg" alt="cpu icon" /> : <img src="./images/cpu.svg" alt="cpu icon" />}
                    </div>
                    <div className="bottomLeftText">

                        <h3>
                            The extension uses a machine learning algorithm to sort your browsing behavior by work or play.  
                        </h3>
                    </div>
                </div>
                <div className="projectItemBottomBox">
                    <div className="bottomIcon">
                    {this.state.detailImage === 2 ? <img src="./images/pie-chartHighlight.svg" alt="pie-chart icon" /> : <img src="./images/pie-chart.svg" alt="pie-chart icon" />}
                    </div>
                    <div className="bottomLeftText">
                        <h3>
                            We then present that data to the user with intuitive and interactive data visualizations.
                        </h3>
                    </div>
                </div>

                </div>
            </div>
        )
    }
}

export default ProjectOne