import React, { useState } from 'react'

const MobileProjectOne = () => {

    const [detailImage, setDetailImage] = useState(0)

    const forward = () => {
        let newNum = 0
        if (detailImage === 2){
            newNum = 0
        } else {
            newNum = detailImage
            newNum++
        }
        setDetailImage(newNum)
    }

    const back = () => {
        let newNum = 0
        if (detailImage === 0){
            newNum = 2
        } else {
            newNum = detailImage
            newNum--
        }
        setDetailImage(newNum)
        console.log(detailImage)
    }

    const returnDetail = () => {
        if (detailImage === 0) {
            return (
                <img src="./images/blogArticle.gif" alt="detail" />
            )
        } else if (detailImage === 1) {
            return (
                <img src="./images/blogBackend.gif" alt="detail" />
            )
        } else if (detailImage === 2) {
            return (
                <img src="./images/blogFrontpage.gif" alt="detail" />            
            )
        }
    }

    const returnCaption = () => {
        if (detailImage === 0) {
            return (
                'From Beyond is a blog app that combines React and Wordpress for the best of both worlds.'
            )
        } else if (detailImage === 1) {
            return (
                `Wordpress's back-end and post editing features are extremely sophisticated, and make the process intuitive for the user.`
            )
        } else if (detailImage === 2) {
            return (
                'Using the Wordpress API, we can hook it up to a React front-end to provide a more modern, unique look to the blog.'          
            )
        }
    }

    const returnIcon = () => {
        if (detailImage === 0) {
            return (
                <img src="./images/book-openHighlight.svg" alt="google chrome icon" />
            )
        } else if (detailImage === 1) {
            return (
                <img src="./images/databaseHighlight.svg" alt="google chrome icon" />
            )
        } else if (detailImage === 2) {
            return (
                <img src="./images/layoutHighlight.svg" alt="google chrome icon" />
                )
        }
    }

    const backgroundChange = () => {
        if (detailImage === 0) {
            return (
                {
                    backgroundColor: 'rgb(230, 156, 131)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else if (detailImage === 1) {
            return (
                {
                    backgroundColor: 'rgb(203, 162, 149)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else {
            return (
                {
                    backgroundColor: 'rgb(193, 120, 111)',
                    transition: 'background-color 2s ease-out'

                }
            )
        }
    }


    return(
        <div id="mobileProjectOne" style={backgroundChange()}>
            <div className="mobileProjectItemTop">
                <div className="mobileDetailImage">
                    <div id="mobileDetailImageContainer">
                        <div>
                            {returnDetail()}
                        </div>
                    </div>
                </div>
                <div className="mobileDetailNavContainer">
                    <div className="mobileDetailNav" onClick={back}>
                            <img src="./images/rewind.svg" alt="back" className="noSelect"  />
                    </div>
                    <div className="mobileDetailNav" onClick={forward}>
                        <img src="./images/fast-forward.svg" alt="forward" className="noSelect"/>
                    </div>
                </div>
            </div>
            <div className="mobileProjectItemBottom">
                <div className="mobileProjectItemBottomBox">
                    <div className="mobileBottomIcon" >
                        {returnIcon()}
                    </div>
                    <div className="mobileBottomLeftText">
                        <h3>
                            {returnCaption()}
                        </h3>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MobileProjectOne