import React, { useState } from 'react'
import { useTrail, animated } from 'react-spring'

const items = [`  Welcome!`, `  my name is Calvin Boetto and I'm a full stack developer.
                I've been making websites`, `  for over 10 years, and have since programmed
                apps, Chrome extensions,`, `  and data visualizations.`]
const config = { mass: 5, tension: 2000, friction: 200 }


const About = () => {
    const [toggle, set] = useState(true)
    const trail = useTrail(items.length, {
      config,
      opacity: toggle ? 1 : 0,
      x: toggle ? -18 : 40,
      height: toggle ? 80 : 0,
      from: { opacity: 0, x: 20, height: 0 },
    })

    function scrollFunction(e) {
        console.log('scroll event', e)
    }

    window.onscroll = scrollFunction;

    return(

        <div id="about" className="trails-main" >
          <div id="aboutTop">
            <div id="aboutQuote">
            <h1>User experience focused</h1>
            <h1>software development.</h1>
            </div>
            <div id="aboutPortrait">
              <div>
                <img src="./images/portrait.png" alt="portrait" />
                <h1>Calvin Boetto</h1>
                <h3>Full Stack Developer</h3>
              </div>
            </div>
          </div>
          <div id="aboutBottom">
            <div id="aboutBottomText">
              {trail.map(({ x, height, ...rest }, index) => (
                <animated.div
                  key={items[index]}
                  className="trails-text"
                  id="bottomTextAbout"
                  style={{ ...rest, transform: x.interpolate(x => `translate3d(0,${x}px,0)`) }}>
                  <animated.div style={{ height }}><p>{items[index]}</p> </animated.div>
                </animated.div>
              ))}
            </div>
          </div>

      </div>
    )

}

export default About