import React, { useState } from 'react'
import { Link } from 'react-router-dom'

const SmallNav = (props) => {

    // Tries to match the pathname or hover target with an item on the navbar.  If it matches, it returns the highlighted versino
    // of the icon.  If it doesn't match, it returns the normal icon.
    
    const highlightIcon = (pathname, hoverIcon) => {
        if (props.pathname === pathname || props.hoverIcon === hoverIcon) {
            return (
                <img src={`images/${hoverIcon}select.png`} alt={hoverIcon} id={`${hoverIcon}Icon`} />
            )
        } else {
            return (
                <img src={`images/${hoverIcon}.png`} alt={hoverIcon} id={`${hoverIcon}Icon`} />
            )
        }
    }


    // Same as above, a matching function for highlighting nav buttons, but modified to work with the projects UL which needs slightly different parameters. 

    const highlightProject = (project) => {
        if (props.pathname === `/${project}` || project === props.hoverIcon) {
            return (
                {color: 'white'}
            )
        } else {
            return (
                {color: 'black'}
            )
        }
    }

    return (
        <div id="smallNav">
            <ul>
                <li>
                    <Link to="/" onMouseEnter={props.toggleOnIcon}>
                        {highlightIcon('/', 'about')}
                    </Link>
                </li>
                <li>
                    <Link to="/skills" onMouseEnter={props.toggleOnIcon}>
                        {highlightIcon('/skills', 'skills')}
                    </Link>
                </li>
                <li >
                    <Link to="/projects" onMouseEnter={props.toggleOnIcon}>
                        {highlightIcon('/projects', 'projects')}
                    </Link>
                        <ul id={props.projectPathnames() ? 'projectsUlTrue' : 'projectsUl'}>
                            <Link to ="/projectone" onMouseEnter={props.toggleOnIcon}>
                                {<li className='projectone' style={highlightProject('projectone')}>1</li>}
                            </Link>
                            <Link to ="/projecttwo" onMouseEnter={props.toggleOnIcon}>
                                <li className='projecttwo' style={highlightProject('projecttwo')}>2</li>
                            </Link>
                            <Link to ="/projectthree" onMouseEnter={props.toggleOnIcon}>
                                <li className='projectthree' style={highlightProject('projectthree')}>3</li>
                            </Link>
                        </ul>
                </li>
                <li>
                    <Link to="/contact" onMouseEnter={props.toggleOnIcon}>
                        {highlightIcon('/contact', 'contact')}
                    </Link>
                </li>
            </ul>
        </div>
    )
}

export default SmallNav