import React, { useState } from 'react'

const MobileProjectOne = () => {

    const [detailImage, setDetailImage] = useState(0)

    const forward = () => {
        let newNum = 0
        if (detailImage === 2){
            newNum = 0
        } else {
            newNum = detailImage
            newNum++
        }
        setDetailImage(newNum)
    }

    const back = () => {
        let newNum = 0
        if (detailImage === 0){
            newNum = 2
        } else {
            newNum = detailImage
            newNum--
        }
        setDetailImage(newNum)
        console.log(detailImage)
    }

    const returnDetail = () => {
        if (detailImage === 0) {
            return (
                <img src="./images/maskotProducts.gif" alt="detail" />
            )
        } else if (detailImage === 1) {
            return (
                <img src="./images/maskotLogin.gif" alt="detail" />
            )
        } else if (detailImage === 2) {
            return (
                <img src="./images/maskotCart.gif" alt="detail" />           
            )
        }
    }

    const returnCaption = () => {
        if (detailImage === 0) {
            return (
                'Maskot is a fully functional e-commerce site for buying fashion-forward flu masks.'
            )
        } else if (detailImage === 1) {
            return (
                'The website supports user registration, administrative controls, and security features.'
            )
        } else if (detailImage === 2) {
            return (
                'Maskot uses the Stripe API to allow for credit card purchases, complete with email confirmations and unique order numbers.'          
            )
        }
    }

    const returnIcon = () => {
        if (detailImage === 0) {
            return (
                <img src="./images/shopping-bagHighlight.svg" alt="google chrome icon" />
            )
        } else if (detailImage === 1) {
            return (
                <img src="./images/usersHighlight.svg" alt="google chrome icon" />
            )
        } else if (detailImage === 2) {
            return (
                <img src="./images/shopping-cartHighlight.svg" alt="google chrome icon" />
                )
        }
    }

    const backgroundChange = () => {
        if (detailImage === 0) {
            return (
                {
                    backgroundColor: 'rgb(172, 181, 163)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else if (detailImage === 1) {
            return (
                {
                    backgroundColor: 'rgb(231, 172, 153)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else {
            return (
                {
                    backgroundColor: 'rgb(230, 156, 131)',
                    transition: 'background-color 2s ease-out'

                }
            )
        }
    }


    return(
        <div id="mobileProjectOne" style={backgroundChange()}>
            <div className="mobileProjectItemTop">
                <div className="mobileDetailImage">
                    <div id="mobileDetailImageContainer">
                        <div>
                            {returnDetail()}
                        </div>
                    </div>
                </div>
                <div className="mobileDetailNavContainer">
                    <div className="mobileDetailNav" onClick={back}>
                            <img src="./images/rewind.svg" alt="back" className="noSelect"  />
                    </div>
                    <div className="mobileDetailNav" onClick={forward}>
                        <img src="./images/fast-forward.svg" alt="forward" className="noSelect"/>
                    </div>
                </div>
            </div>
            <div className="mobileProjectItemBottom">
                <div className="mobileProjectItemBottomBox">
                    <div className="mobileBottomIcon" >
                        {returnIcon()}
                    </div>
                    <div className="mobileBottomLeftText">
                        <h3>
                            {returnCaption()}
                        </h3>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MobileProjectOne