import React, { useState } from 'react'
import ArrowKeysReact from 'arrow-keys-react'


class ProjectOne extends React.Component {


    constructor(){
        super()
        this.state = {
            detailImage: 0,
            hover: false
        }
        this.forward = this.forward.bind(this)
        this.back = this.back.bind(this)
        this.returnDetail = this.returnDetail.bind(this)
        this.returnDetailHover = this.returnDetailHover.bind(this)
        this.backgroundChange = this.backgroundChange.bind(this)
        this.toggleHoverOn = this.toggleHoverOn.bind(this)
        this.toggleHoverOff = this.toggleHoverOff.bind(this)
    }

    forward = () => {
        let newNum = 0
        if (this.state.detailImage === 2){
            newNum = 0
        } else {
            newNum = this.state.detailImage
            newNum++
        }
        this.setState({
            detailImage: newNum
        })
        console.log(this.state.detailImage)
    }

    back = () => {
        let newNum = 0
        if (this.state.detailImage === 0){
            newNum = 2
        } else {
            newNum = this.state.detailImage
            newNum--
        }
        this.setState({
            detailImage: newNum
        })
        console.log(this.state.detailImage)
    }

    returnDetail = () => {
        if (this.state.detailImage === 0) {
            return (
                <img src="./images/maskotProducts.png" alt="detail" />
            )
        } else if (this.state.detailImage === 1) {
            return (
                <img src="./images/maskotLogin.png" alt="detail" />
            )
        } else if (this.state.detailImage === 2) {
            return (
                <img src="./images/maskotCart.png" alt="detail" />            
            )
        }
    }

    returnDetailHover = () => {
        if (this.state.detailImage === 0) {
            return (
                <img src="./images/maskotProducts.gif" alt="detail" />
            )
        } else if (this.state.detailImage === 1) {
            return (
                <img src="./images/maskotLogin.gif" alt="detail" />
            )
        } else if (this.state.detailImage === 2) {
            return (
                <img src="./images/maskotCart.gif" alt="detail" />            
            )
        }
    }

    toggleHoverOn = () => {
        this.setState( {
            hover: true
        })
    }

    toggleHoverOff = () => {
        this.setState({
            hover: false
        })
    }

    backgroundChange () {
        if (this.state.detailImage === 0) {
            return (
                {
                    backgroundColor: 'rgb(172, 181, 163)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else if (this.state.detailImage === 1) {
            return (
                {
                    backgroundColor: 'rgb(231, 172, 153)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else {
            return (
                {
                    backgroundColor: 'rgb(230, 156, 131)',
                    transition: 'background-color 2s ease-out'

                }
            )
        }
    }


    render(){

        ArrowKeysReact.config({
            left: () => {
                if (this.state.detailImage === 0 ) {
                    this.setState({
                        detailImage: 2
                    })
                } else {
                    let tempnum = this.state.detailImage
                    tempnum--
                    this.setState({
                        detailImage: tempnum
                    })
                }
              },
            right: () => {
              if (this.state.detailImage === 2 ) {
                  this.setState({
                      detailImage: 0
                  })
              } else {
                  let tempnum = this.state.detailImage
                  tempnum++
                  this.setState({
                      detailImage: tempnum
                  })
              }
            }
        })


    return(
        <div id="projectOne" style={this.backgroundChange()}>
            <div className="projectItemTop">
                    <div className="detailNav" onClick={this.back}>
                        <div>
                            <img src="./images/rewind.svg" alt="back" className="noSelect"  />
                        </div>
                    </div>
                    <div className="detailImage" onMouseEnter={this.toggleHoverOn} onMouseLeave={this.toggleHoverOff}>
                        <div id="detailImageContainer">
                            {this.state.hover === false ? this.returnDetail() : this.returnDetailHover() }
                        </div>
                    </div>
                    <div className="detailNav"  onClick={this.forward}>
                        <img src="./images/fast-forward.svg" alt="forward" className="noSelect"/>
                    </div>
            </div>
            <div className="projectItemBottom">
                <div className="projectItemBottomBox">
                    <div className="bottomIcon">
                        {this.state.detailImage === 0 ? <img src="./images/shopping-bagHighlight.svg" alt="shopping-bag icon" /> : <img src="./images/shopping-bag.svg" alt="shopping-bag icon" />}
                    </div>
                    <div className="bottomLeftText">

                        <h3>
                            Maskot is a fully functional e-commerce site for buying fashion-forward flu masks.  
                        </h3>
                    </div>
                </div>
                <div className="projectItemBottomBox">
                    <div className="bottomIcon">
                        {this.state.detailImage === 1 ? <img src="./images/usersHighlight.svg" alt="users icon" /> : <img src="./images/users.svg" alt="users icon" />}
                    </div>
                    <div className="bottomLeftText">

                        <h3>
                            The website supports user registration, administrative controls, and security features to make sure users are where they should be.  
                        </h3>
                    </div>
                </div>
                <div className="projectItemBottomBox">
                    <div className="bottomIcon">
                        {this.state.detailImage === 2 ? <img src="./images/shopping-cartHighlight.svg" alt="shopping-cart icon" /> : <img src="./images/shopping-cart.svg" alt="shopping-cart icon" />}
                    </div>
                    <div className="bottomLeftText">
                        <h3>
                            Maskot uses the Stripe API to allow for credit card purchases, complete with email confirmations and unique order numbers.
                        </h3>
                    </div>
                </div>

                </div>
            </div>
        )
    }
}

export default ProjectOne