import React, { useState } from 'react'

const MobileProjectOne = () => {

    const [detailImage, setDetailImage] = useState(0)

    const forward = () => {
        let newNum = 0
        if (detailImage === 2){
            newNum = 0
        } else {
            newNum = detailImage
            newNum++
        }
        setDetailImage(newNum)
    }

    const back = () => {
        let newNum = 0
        if (detailImage === 0){
            newNum = 2
        } else {
            newNum = detailImage
            newNum--
        }
        setDetailImage(newNum)
        console.log(detailImage)
    }

    const returnDetail = () => {
        if (detailImage === 0) {
            return (
                <iframe width="100%" height="auto" src="https://www.youtube.com/embed/2Q_PyxAE3Mo" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
            )
        } else if (detailImage === 1) {
            return (
                <img src="./images/wireheadtraining.gif" alt="detail static" />
            )
        } else if (detailImage === 2) {
            return (
                <img src="./images/wireheaddataviz.gif" alt="detail static" />            
            )
        }
    }

    const returnCaption = () => {
        if (detailImage === 0) {
            return (
                'Wirehead is a Google Chrome Extension that helps you understand your browsing behavior.'
            )
        } else if (detailImage === 1) {
            return (
                'The extension uses a machine learning algorithm to sort your browsing behavior by work or play.'
            )
        } else if (detailImage === 2) {
            return (
                'We then present that data to the user with intuitive and interactive data visualizations.'          
            )
        }
    }

    const returnIcon = () => {
        if (detailImage === 0) {
            return (
                <img src="./images/chromeHighlight.svg" alt="google chrome icon" />
            )
        } else if (detailImage === 1) {
            return (
                <img src="./images/cpuHighlight.svg" alt="google chrome icon" />
            )
        } else if (detailImage === 2) {
            return (
                <img src="./images/pie-chartHighlight.svg" alt="google chrome icon" />
                )
        }
    }

    const backgroundChange = () => {
        if (detailImage === 0) {
            return (
                {
                    backgroundColor: 'rgb(128, 160, 165)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else if (detailImage === 1) {
            return (
                {
                    backgroundColor: 'rgb(148, 170, 149)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else {
            return (
                {
                    backgroundColor: 'rgb(172, 181, 163)',
                    transition: 'background-color 2s ease-out'

                }
            )
        }
    }


    return(
        <div id="mobileProjectOne" style={backgroundChange()}>
            <div className="mobileProjectItemTop">
                <div className="mobileDetailImage">
                    <div id="mobileDetailImageContainer">
                        <div>
                            {returnDetail()}
                        </div>
                    </div>
                </div>
                <div className="mobileDetailNavContainer">
                    <div className="mobileDetailNav" onClick={back}>
                            <img src="./images/rewind.svg" alt="back" className="noSelect"  />
                    </div>
                    <div className="mobileDetailNav" onClick={forward}>
                        <img src="./images/fast-forward.svg" alt="forward" className="noSelect"/>
                    </div>
                </div>
            </div>
            <div className="mobileProjectItemBottom">
                <div className="mobileProjectItemBottomBox">
                    <div className="mobileBottomIcon" >
                        {returnIcon()}
                    </div>
                    <div className="mobileBottomLeftText">
                        <h3>
                            {returnCaption()}
                        </h3>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MobileProjectOne