import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import ArrowKeysReact from 'arrow-keys-react'
import WheelReact from 'wheel-react'
import Navbar from './components/navbar.js'
import Content from './components/content.js'
import Header from './components/header.js'

import MobileNavbar from './components/mobile/mobileNavbar.js'
import MobileContent from './components/mobile/mobileContent.js'

import './App.css';

class App extends Component {

  constructor(props){
    super(props)
    this.state = {
      hover: false,
      x: 0, 
      y: 0,
      height: window.innerHeight, 
      width: window.innerWidth
    }
    this.updateDimensions = this.updateDimensions.bind(this);
    this.toggleOn = this.toggleOn.bind(this)
    this.toggleOff = this.toggleOff.bind(this)
  }


  _onMouseMove(e) {
    this.setState({ x: e.screenX, y: e.screenY })
  }

  updateDimensions() {
    this.setState({
      height: window.innerHeight, 
      width: window.innerWidth
    });
  }

  toggleOn() {
    this.setState({
      hover: true
    })
  }

  toggleOff() {
    this.setState({
      hover: false
    })
  }

  componentDidMount(){
    if (document.getElementById('App')) {
      document.getElementById('App').focus();
    }
    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions)
  }

  /**
   * Remove event listener
   */
  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions.bind(this));
  }



  render() {
    return (        
      <Router>
        {this.state.width < 600 ?
        <>
          <Route to="/" component={MobileNavbar} />
          <Route to="/" component={MobileContent} />
        </>
        :
        <div  {...ArrowKeysReact.events} {...WheelReact.events} tabIndex="1" className="App" id="App" onMouseMove={this._onMouseMove.bind(this)}>
            <div id="top" style={this.state.height < 696 ? {height: '0px', overflow: 'hidden', transition: 'height 0.2s ease-out'} : {overflow: 'hidden'}}>
              <Header x={this.state.x} height={this.state.height} />
              {/* <SlideMenu /> */}
            </div>
            <div id="middle">
              <Route to="/" component={Navbar} onMouseEnter={this.toggleOn} onMouseOut={this.toggleOff} props={this.state.hover} />
              <div id="content" onMouseEnter={this.toggleOff}>
                <Route to="/" component={Content} />
              </div>
            </div>
    
        </div>
        }
      </Router>

    );
  }
}

export default App;
