import React, { useState } from 'react'
import { Link } from 'react-router-dom'


const item1 = {
  title: 'Wirehead',
  caption: 'A Google Chrome Extension',
  picture: './images/wirehead.jpg',
  link: 'projectone'
}

const item2 = {
  title: 'Maskot',
  caption: 'An e-Commerce Website',
  picture: './images/maskot.png',
  link: 'projecttwo'
}

const item3 = {
  title: 'From Beyond',
  caption: 'A React/WordPress Blog',
  picture: './images/blog.jpg',
  link: 'projectthree'
}

const item4 = {
  title: 'Current Project',
  caption: 'A Mini Social Media App',
  picture: './images/chat.png',
  link: null
}

const galleryTop = [item1, item2]
const galleryBottom = [item3, item4]

const mapGallery = (gallery) => gallery.map(element => 
  <div className="galleryItem">
    <Link to={element.link}>
    <div className="galleryPicture">
      <img src={element.picture} alt="portfolio item" />
    </div>
    <div className="galleryCaption">
      <h3>{element.title} : {element.caption}</h3>
    </div>
    </Link>
  </div>
  
  )

class Projects extends React.Component {
    render() {
    
        return (
          <div id="projects">
            <div id="projectsTop">
              {mapGallery(galleryTop)}
            </div>
            <div id="projectsBottom">
              {mapGallery(galleryBottom)}
            </div>
          </div>
        )
      }
}
 
export default Projects;