import React from 'react'
import { Route, Switch } from 'react-router-dom'

import MobileAbout from './mobileAbout.js'
import MobileSkills from './mobileSkills.js'
import MobileProjects from './mobileProjects.js'
import MobileProjectOne from './mobileProjectOne.js'
import MobileProjectTwo from './mobileProjectTwo.js'
import MobileProjectThree from './mobileProjectThree.js'
import MobileContact from './mobileContact.js'



const MobileContent = () => {

    return (
            <div id="miniContent">
              <Switch>
                <Route exact path="/" component={MobileAbout} />
                <Route path="/skills" component={MobileSkills} />
                <Route path="/projects" component={MobileProjects} />
                <Route path="/projectone" component={MobileProjectOne} />
                <Route path="/projecttwo" component={MobileProjectTwo} />
                <Route path="/projectthree" component={MobileProjectThree} />
                <Route path="/contact" component={MobileContact} />
              </Switch>
            </div>
    )
            
    }
    

export default MobileContent