import React from 'react'
import {Link} from 'react-router-dom'

const MobileProjects = () => {

    const item1 = {
        title: 'Wirehead',
        caption: 'Chrome Tool',
        picture: './images/wirehead.jpg',
        link: 'projectone'
      }
      
      const item2 = {
        title: 'Maskot',
        caption: 'eCommerce App',
        picture: './images/maskot.png',
        link: 'projecttwo'
      }
      
      const item3 = {
        title: 'From Beyond',
        caption: 'React Blog',
        picture: './images/blog.jpg',
        link: 'projectthree'
      }
      
      const item4 = {
        title: 'Current Project',
        caption: 'Social Media App',
        picture: './images/chat.png',
        link: null
      }
      
      const galleryTop = [item1, item2]
      const galleryBottom = [item3, item4]
      
      const mapGallery = (gallery) => gallery.map(element => 
        <div className="mobileGalleryItem">
          <Link to={element.link}>
          <div className="mobileGalleryPicture">
            <img src={element.picture} alt="portfolio item" />
          </div>
          <div className="mobileGalleryCaption">
            <h4 className="mobileCaption">{element.title} : <br /> {element.caption}</h4>
          </div>
          </Link>
        </div>
        
        )

    return(
        <div id="mobileProjects">
            <div id="mobileProjectsTop">
                {mapGallery(galleryTop)}
            </div>
            <div id="mobileProjectsBottom">
                {mapGallery(galleryBottom)}
            </div>
        </div>
    )
}

export default MobileProjects
