import React, { useState } from 'react'
import ArrowKeysReact from 'arrow-keys-react'


class ProjectOne extends React.Component {


    constructor(){
        super()
        this.state = {
            detailImage: 0,
            hover: false
        }
        this.forward = this.forward.bind(this)
        this.back = this.back.bind(this)
        this.returnDetail = this.returnDetail.bind(this)
        this.returnDetailHover = this.returnDetailHover.bind(this)
        this.toggleHoverOn = this.toggleHoverOn.bind(this)
        this.toggleHoverOff = this.toggleHoverOff.bind(this)
        this.backgroundChange = this.backgroundChange.bind(this)
    }

    forward = () => {
        let newNum = 0
        if (this.state.detailImage === 2){
            newNum = 0
        } else {
            newNum = this.state.detailImage
            newNum++
        }
        this.setState({
            detailImage: newNum
        })
        console.log(this.state.detailImage)
    }

    back = () => {
        let newNum = 0
        if (this.state.detailImage === 0){
            newNum = 2
        } else {
            newNum = this.state.detailImage
            newNum--
        }
        this.setState({
            detailImage: newNum
        })
        console.log(this.state.detailImage)
    }

    returnDetail = () => {
        if (this.state.detailImage === 0) {
            return (
                <img src="./images/blogArticle.png" alt="detail" />
            )
        } else if (this.state.detailImage === 1) {
            return (
                <img src="./images/blogBackend.png" alt="detail" />
            )
        } else if (this.state.detailImage === 2) {
            return (
                <img src="./images/blogFrontpage.png" alt="detail" />            
            )
        }
    }

    returnDetailHover = () => {
        if (this.state.detailImage === 0) {
            return (
                <img src="./images/blogArticle.gif" alt="detail" />
            )
        } else if (this.state.detailImage === 1) {
            return (
                <img src="./images/blogBackend.gif" alt="detail" />
            )
        } else if (this.state.detailImage === 2) {
            return (
                <img src="./images/blogFrontpage.gif" alt="detail" />            
            )
        }
    }

    toggleHoverOn = () => {
        this.setState( {
            hover: true
        })
    }

    toggleHoverOff = () => {
        this.setState({
            hover: false
        })
    }

    backgroundChange () {
        if (this.state.detailImage === 0) {
            return (
                {
                    backgroundColor: 'rgb(230, 156, 131)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else if (this.state.detailImage === 1) {
            return (
                {
                    backgroundColor: 'rgb(203, 162, 149)',
                    transition: 'background-color 2s ease-out'

                }
            )
        } else {
            return (
                {
                    backgroundColor: 'rgb(193, 120, 111)',
                    transition: 'background-color 2s ease-out'

                }
            )
        }
    }


    render(){

        ArrowKeysReact.config({
            left: () => {
                if (this.state.detailImage === 0 ) {
                    this.setState({
                        detailImage: 2
                    })
                } else {
                    let tempnum = this.state.detailImage
                    tempnum--
                    this.setState({
                        detailImage: tempnum
                    })
                }
              },
            right: () => {
              if (this.state.detailImage === 2 ) {
                  this.setState({
                      detailImage: 0
                  })
              } else {
                  let tempnum = this.state.detailImage
                  tempnum++
                  this.setState({
                      detailImage: tempnum
                  })
              }
            }
        })


    return(
        <div id="projectOne" style={this.backgroundChange()}>
            <div className="projectItemTop">
                    <div className="detailNav" onClick={this.back}>
                        <div>
                            <img src="./images/rewind.svg" alt="back" className="noSelect"  />
                        </div>
                    </div>
                    <div className="detailImage" onMouseEnter={this.toggleHoverOn} onMouseLeave={this.toggleHoverOff}>
                        <div id="detailImageContainer">
                            {this.state.hover === false ? this.returnDetail() : this.returnDetailHover() }
                        </div>
                    </div>
                    <div className="detailNav"  onClick={this.forward}>
                        <img src="./images/fast-forward.svg" alt="forward" className="noSelect"/>
                    </div>
            </div>
            <div className="projectItemBottom">
                <div className="projectItemBottomBox">
                    <div className="bottomIcon">
                        {this.state.detailImage === 0 ? <img src="./images/book-openHighlight.svg" alt="book-open icon" /> : <img src="./images/book-open.svg" alt="book-open icon" />}
                    </div>
                    <div className="bottomLeftText">

                        <h3>
                            From Beyond is a blog app that combines React and Wordpress for the best of both worlds.  
                        </h3>
                    </div>
                </div>
                <div className="projectItemBottomBox">
                    <div className="bottomIcon">
                        {this.state.detailImage === 1 ? <img src="./images/databaseHighlight.svg" alt="database icon" /> : <img src="./images/database.svg" alt="database icon" />}
                    </div>
                    <div className="bottomLeftText">

                        <h3>
                            Wordpress's back-end and post editing features are extremely sophisticated, and make the process intuitive for the user.  
                        </h3>
                    </div>
                </div>
                <div className="projectItemBottomBox">
                    <div className="bottomIcon">
                        {this.state.detailImage === 2 ? <img src="./images/layoutHighlight.svg" alt="layout icon" /> : <img src="./images/layout.svg" alt="layout icon" />}
                    </div>
                    <div className="bottomLeftText">
                        <h3>
                            Using the Wordpress API, we can hook it up to a React front-end to provide a more modern, unique look to the blog.
                        </h3>
                    </div>
                </div>

                </div>
            </div>
        )
    }
}

export default ProjectOne